import React, {useState} from 'react';
import {Grid, Button, Divider} from 'semantic-ui-react';
import {multipartApiCall} from "../services/api";

const SaveToDrive = (props)=>{
	 const browserLang = props.browserLang || "en";
	 const userId = props.userId ? props.userId : null; 
	 const foldername = props.foldername? props.foldername : "";
	 const files = props.files ? props.files : [];
	 const [loading, setLoading] = useState(false);
	 const fileDataArr = props.fileDataArr ? props.fileDataArr : null;
	 console.log("fileDataArr",fileDataArr);
	 const sendToDrive = async (filesArr) => {
	 	try{
	 		setLoading(true);
	 		if(fileDataArr){
				for (const file of fileDataArr){
					let lineArray = [];
					if(file.data.length > 1){
							file.data.forEach((infoArray, index)=> {
						    let line = infoArray.join(",");
						    lineArray.push(line);
						});
						let csvContent = lineArray.join("\n");
						const f = new File([csvContent], `${file.filename}.csv`, {type: "text/csv", lastModified: new Date()})
						files.push(f);
					}
				} 
		 	}

	 		const formData = new FormData();
		      for(let file of files){
		        formData.append("image", file);
		      }
		      formData.append("userid", userId);
		      formData.append("foldername",foldername);
		      formData.append("largeFilesExpected",JSON.stringify(false));
		      const uploadedFiles = await multipartApiCall("POST",`${process.env.REACT_APP_PRODUCTION_SERVER_URL}/api/images/uploads/drive/bulk`, formData);
		      setLoading(false);
	 	}catch(err){
	 		console.log(err);
	 		setLoading(false);
	 	}
	 };
	return(
	<div>
		<Button loading={loading} color="teal" onClick={sendToDrive}>
			{browserLang === "en" ? "Save": "Guardar"}
		</Button>
	</div>
	);
}
export default SaveToDrive;